.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems{
    padding: 10px;
    color: white;
}

.dialogsItems > .active{
    color: gold;
}

.dialog a{
    color: green;
    text-decoration: none;
}

.messages{
    padding: 10px;
}

.message{
    color: white;
}